<template>
    <div class="home">
        <p>Hello World</p>

        <p>{{ allEmail }}</p>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "Home",

        data: () => ({
            allEmail: []
        }),

        async created() {
            await this.getAllEmail()
        }, 

        methods: {
            // retrieve all email
            async getAllEmail() {
                console.log("Hello")
                try {  
                    const url = process.env.VUE_APP_URL_MAIL_API

                    console.log(url)

                    const res = await axios.get(url + '/mail')

                    this.allEmail = res

                } catch(err) {
                    console.log(err)
                }
            }
        }
    }

</script>